.inquiry-page-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #fff;
  color: #333;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.inquiry-page-container h1 {
  text-align: center;
  margin-bottom: 2rem;
}

.inquiry-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
  padding: 1.5rem;
  border: 1px solid #eee;
  border-radius: 8px;
}

.inquiry-form h2 {
  text-align: center;
  margin-top: 0;
}

.inquiry-form .form-row {
  display: flex;
  gap: 1rem;
}

.inquiry-form .form-row input {
  flex: 1;
}

.inquiry-form input,
.inquiry-form textarea,
.inquiry-form input[type="file"] {
  width: 100%;
  padding: 0.8rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.inquiry-form textarea {
  min-height: 120px;
  resize: vertical;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

.inquiry-form button {
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.inquiry-form button.cancel-btn {
  background-color: #888;
}

.inquiry-form button:hover {
  background-color: #000;
}

.inquiry-form button.cancel-btn:hover {
  background-color: #666;
}

.write-btn-container {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.write-btn {
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.write-btn:hover {
  background-color: #000;
}

.inquiry-list table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  border-top: 2px solid #333;
  border-bottom: 1px solid #ccc;
}

.inquiry-list th,
.inquiry-list td {
  padding: 0.8rem 1rem;
  border-bottom: 1px solid #eee;
  vertical-align: middle;
}

.inquiry-list th {
  background-color: #f8f9fa;
  font-weight: bold;
  text-align: center;
}

.inquiry-list .col-title {
  text-align: left;
}

.inquiry-list .col-title .title-button {
  background: none;
  border: none;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  padding: 0;
  font: inherit;
  text-align: left;
}

.inquiry-list .col-title .title-button:hover {
  color: #005aab;
  text-decoration: underline;
}

.inquiry-list .col-index {
  width: 8%;
  text-align: center;
}

.inquiry-list .col-title {
  width: auto;
}

.inquiry-list .col-author {
  width: 12%;
  text-align: center;
}

.inquiry-list .col-date {
  width: 15%;
  text-align: center;
}

.inquiry-list .col-views {
  width: 10%;
  text-align: center;
}

.inquiry-list td.col-manage {
  width: 15%;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  justify-content: center;
  align-items: center;
  /* Horizontally center the buttons */
}

.col-manage button {
  font-size: 0.8rem;
  padding: 0.3rem 0.8rem;
  /* Adjusted padding */
  border-radius: 4px;
  border: 1px solid transparent;
  cursor: pointer;
  min-width: 50px;
  /* Give a minimum width */
}

.col-manage .edit-btn {
  background-color: #5bc0de;
  color: white;
}

.inquiry-list .col-manage .inquiry-delete-btn {
  background-color: #d9534f;
  color: white;
}

/* Styles for photo in modal */
.modal-photo {
  max-width: 100%;
  border-radius: 4px;
  margin-bottom: 1rem;
}

/* Quill Editor Styles */
.quill-container {
  margin: 1rem 0;
}

.quill-container .ql-editor {
  min-height: 180px;
}

/* Quill Editor Rendered Content in Modal */
.modal-body .ql-editor {
  font-size: 1rem;
  line-height: 1.6;
  padding: 0;
}

/* Images in modal - auto-fit without cropping */
.modal-body .ql-editor img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 1rem auto;
  border-radius: 8px;
  object-fit: contain;
}

.modal-body .ql-editor iframe,
.modal-body .ql-editor .ql-video {
  max-width: 100%;
  width: 100%;
  height: 400px;
  border-radius: 8px;
  margin: 1rem 0;
}

/* Make videos resizable in editor */
.quill-container .ql-editor iframe,
.quill-container .ql-editor .ql-video {
  max-width: 100%;
  height: 400px;
  resize: both;
  overflow: auto;
}

/* Mobile Responsive Design */
@media (max-width: 768px) {
  .inquiry-page-container {
    padding: 1.5rem;
    margin: 1rem auto;
  }

  .inquiry-page-container h1 {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }

  .inquiry-form {
    padding: 1rem;
  }

  .inquiry-form h2 {
    font-size: 1.4rem;
  }

  .inquiry-form input,
  .inquiry-form textarea {
    padding: 0.7rem;
    font-size: 0.95rem;
  }

  .inquiry-form button {
    padding: 0.7rem 1.2rem;
    font-size: 0.95rem;
  }

  .write-btn {
    padding: 0.5rem 1rem;
    font-size: 0.95rem;
  }

  /* Table responsive - horizontal scroll */
  .inquiry-list {
    overflow-x: auto;
  }

  .inquiry-list table {
    min-width: 600px;
  }

  .inquiry-list th,
  .inquiry-list td {
    padding: 0.6rem 0.75rem;
    font-size: 0.9rem;
  }

  .modal-body .ql-editor iframe,
  .modal-body .ql-editor .ql-video {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .inquiry-page-container {
    padding: 1rem;
    margin: 0.5rem auto;
  }

  .inquiry-page-container h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }

  .inquiry-form {
    padding: 0.75rem;
    gap: 0.75rem;
  }

  .inquiry-form h2 {
    font-size: 1.2rem;
  }

  .inquiry-form .form-row {
    flex-direction: column;
    gap: 0.75rem;
  }

  .inquiry-form input,
  .inquiry-form textarea {
    padding: 0.6rem;
    font-size: 0.9rem;
  }

  .inquiry-form textarea {
    min-height: 100px;
  }

  .inquiry-form button {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }

  .write-btn {
    padding: 0.5rem 0.9rem;
    font-size: 0.9rem;
  }

  /* Table responsive - horizontal scroll with smaller size */
  .inquiry-list {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }

  .inquiry-list table {
    min-width: 500px;
    font-size: 0.85rem;
  }

  .inquiry-list th,
  .inquiry-list td {
    padding: 0.5rem 0.5rem;
    font-size: 0.85rem;
  }

  .inquiry-list .col-index {
    width: 10%;
  }

  .inquiry-list .col-author {
    width: 15%;
  }

  .inquiry-list .col-date {
    width: 20%;
  }

  .inquiry-list .col-views {
    width: 12%;
  }

  .col-manage button {
    font-size: 0.75rem;
    padding: 0.25rem 0.6rem;
    min-width: 45px;
  }

  .modal-body .ql-editor iframe,
  .modal-body .ql-editor .ql-video {
    height: 250px;
  }

  .modal-body .ql-editor {
    font-size: 0.9rem;
  }
}

/* Confirm Modal */
.confirm-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}

.confirm-modal {
  background: #fff;
  padding: 30px 40px;
  border: 2px solid #333;
  border-radius: 8px;
  max-width: 400px;
  text-align: center;
}

.confirm-modal p {
  margin: 0 0 25px 0;
  font-size: 1.1rem;
  font-weight: 500;
  color: #333;
}

.confirm-modal-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.confirm-modal-actions button {
  padding: 12px 24px;
  border: 1px solid #333;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 4px;
  transition: all 0.2s;
}

.confirm-modal-actions .cancel-btn {
  background: #fff;
  color: #333;
}

.confirm-modal-actions .cancel-btn:hover {
  background: #f0f0f0;
}

.confirm-modal-actions .delete-btn {
  background: #d9534f;
  color: #fff;
  border-color: #d9534f;
}

.confirm-modal-actions .delete-btn:hover {
  background: #c9302c;
}